<template>
  <div id="login">
    <div id="login_head">
      <img src="../../static/img/logoaaaa.png" alt="" />
      <p>欢 迎 进 入 运 政 治 超 系 统</p>
    </div>
    <div class="login_center">
      <div class="login_text">
        <i class="iconfont icon-tubiao211 ilogin1"></i>
        <input type="text" name="" value="" placeholder="请 输 入 手 机 号 码"/>
      </div>
    </div>
    <div class="login_center">
      <div class="login_text">
        <i class="iconfont icon-mima1 ilogin1"></i>
        <input type="password" name="" value="" placeholder="请 输 入 密 码"/>
      </div>
    </div>
    <div id="login_code">
      <div id="login_import">
        <i class="iconfont icon-yanzhengma ilogin1"></i>
        <input type="text" name="" value="" placeholder="请 输 入验 证 码" style="width:1.3rem;height:0.4rem;border:none;text-indent: 0.15rem"/>
      </div>
      <input type="button" id="login_click" value="获取验证码" ref="mybox" @click="time()" />
    </div>
    <div id="login_password">
      <div class="login_rember">
        <input type="checkbox" name="" id="" value="" />
        <span>记住密码</span>
      </div>
      <a href="#">忘 记 密 码 ?</a>
    </div>
    <router-link to="/personal" class="login_submit">
      <p>登  录</p>
    </router-link>
    <div class="login_submit">
      <p>新 用 户 注 册</p>
    </div>
    <div class="remind">
      <p>安裕智能提醒您：为了数据安全，请注册后使用</p>
    </div>

  </div>
</template>

<script type="text/ecmascript-6">
    export default {
      data(){
        return{
          wait:60
        }
      },
      methods:{
        time() {
          var that=this;
          if (this.wait == 0) {
            this.$refs.mybox.removeAttribute("disabled");
            this.$refs.mybox.value="获取验证码";
            this.wait = 60;
          } else {
            this.$refs.mybox.setAttribute("disabled", true);
            this.$refs.mybox.value=this.wait+"秒后重新发送";
            this.wait--;
            setTimeout(function() {
                that.time()
              },
              1000)
          }
        },
      }

    }
</script>

<style scoped>

</style>



