import Vue from 'vue'
import Router from 'vue-router'
import Login from '../components/login.vue'
import Personal from '../components/personal.vue'

Vue.use(Router)

export default new Router({
  routes: [
    {
      path: '/',
      name: 'Login',
      component: Login
    },
    {
      path: '/personal',
      name: 'Personal',
      component: Personal
    }
  ]
})



