import Vue from 'vue'
import App from './App'
import router from './router'
import '../static/css/public.css'
import '../static/css/login.css'
import '../static/css/myself.css'
import '../static/js/jquery-1.8.3.min'

Vue.config.productionTip = false


new Vue({
  el: '#app',
  router,
  components: { App },
  template: '<App/>'
})



